body {
  margin: 0;
  padding: 0;
  // width: 100%;
  // height: 100%;
  position: relative;
  font-family: var(--font-family-futura);
  font-size: 16px;
  line-height: 120%;
  font-weight: 400;
  color: var(--black);

  @media (max-width: 768px) {
    font-size: 14px;
  }
}

html {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  // overflow-x: hidden;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}


* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

*::-webkit-scrollbar {
  width: 0;
}

*::-webkit-scrollbar-thumb {
  background-color: transparent;
}

a {
  display: inline-flex;
  color: inherit;
  font-weight: inherit;
  font-style: inherit;
  font-family: inherit;
  text-decoration: none;
  cursor: pointer;
  margin: 0;
}

button {
  background-color: transparent;
  border: none;
  display: inline-flex;
  outline: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  line-height: initial;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

picture {
  display: inline-flex;
}

fieldset {
    padding: 0;
    outline: none;
    border: none;
}

input {
  width: 100%;
  outline: none;
  border: none;
}

textarea {
    width: 100%;
    outline: none;
    border: none;
}

label {
  display: block;
  position: relative;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  position: relative;
}

svg {
  fill: transparent;
}

.body-scroll {
    position: fixed;
    overflow-y: hidden;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
  background-color: var(--bg);
  display: flex;
  flex-direction: column;
}

.sticky {
  // position: fixed !important;
  // top: 10px;
  // z-index: 50 !important;
  // overflow-x: scroll;
}

.btn-blue {
    padding: 11px 28px;
    font-size: 18px;
    font-weight: 500;
    color: var(--white);
    border-radius: 16px;
    background-color: var(--green);
    transition: all 0.3s ease;

    &:hover {
      background-color: var(--yellow);
    }

    &:active {
      background-color: var(--gold);
    }

    @media (max-width: 480px) {
      padding: 10px 18px;
      font-size: 14px;
    }
}

.btn-white {
  padding: 11px 28px;
  font-size: 18px;
  font-weight: 500;
  color: var(--green);
  border-radius: 16px;
  border: 1px solid var(--green);
  background-color: var(--white);
  transition: all 0.3s ease;

  &:hover {
    color: var(--yellow);
    border: 1px solid var(--yellow);
  }

  &:active {
    color: var(--gray);
    border: 1px solid var(--gray);
  }

  @media (max-width: 480px) {
    padding: 10px 18px;
    font-size: 14px;
  }
}