.filter__select-body {
  display: none;
  flex-direction: column;
  grid-gap: 3px;
  margin-top: 10px;
  width: 100%;
  padding: 10px 0;
  background-color: var(--white);
  border-radius: 12px;
  box-shadow: 0px 4px 15px rgba(212, 212, 212, 0.64);
}
.filter__select-item {
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.filter__select-item:hover {
  background-color: rgba(100, 182, 172, 0.3);
  color: var(--green);
}
.filter__checkbox-label {
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}
@media (max-width: 1250px) {
  .filter__checkbox-label {
    margin-bottom: 0;
  }
}
.filter__checkbox-input {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.3s ease;
}
.filter__checkbox-input:checked ~ .filter__checkbox-name::after {
  opacity: 1;
}
.filter__checkbox-input:checked ~ .filter__checkbox-name::before {
  background-color: var(--white);
  border: none;
}
.filter__checkbox-name {
  position: relative;
  padding-left: 34px;
  font-size: 18px;
  color: var(--gray);
  transition: all 0.3s ease;
  cursor: pointer;
  white-space: nowrap;
}
.filter__checkbox-name::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--white);
  width: 24px;
  height: 24px;
  border: 2px solid var(--light-green);
  border-radius: 6px;
  transition: all 0.3s ease;
  z-index: 1;
}
.filter__checkbox-name::after {
  content: "";
  position: absolute;
  display: block;
  background-image: var(--arrow-check);
  background-size: cover;
  background-repeat: no-repeat;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  z-index: 5;
  opacity: 0;
  transition: all 0.3s ease;
}
.filter__checkbox-name:hover {
  transition: all 0.3s ease;
}
.filter__checkbox-name:hover::before {
  background-color: var(--light-green);
}
@media (max-width: 768px) {
  .filter__checkbox-name {
    font-size: 16px;
  }
}
.filter__input-block {
  display: flex;
  align-items: center;
  column-gap: 14px;
  font-family: var(--font-family-futura);
  font-weight: 400;
  font-size: 18px;
  line-height: 19px;
}
@media (max-width: 768px) {
  .filter__input-block {
    font-size: 16px;
  }
}
@media (max-width: 480px) {
  .filter__input-block {
    flex-wrap: wrap;
  }
}
.filter__input-name {
  white-space: nowrap;
}
@media (max-width: 480px) {
  .filter__input-name {
    flex: 0 0 100%;
  }
}
.filter__input {
  height: 44px;
  width: 60px;
  border: 2px solid var(--light-green);
  border-radius: 40px;
  text-align: center;
}
@media (max-width: 768px) {
  .filter__input {
    font-size: 16px;
  }
}