
.sidebar {
    flex: 0 0 315px;
    background: #DAEAE8;
    overflow: hidden;

    @media (max-width: 768px) {
        flex: 0 0 56px;
        height: 100%;
        margin-top: 29px;
        border-radius: 0 33px 33px 0;
    }

    @media (max-width: 480px) {
        border-radius: 33px 33px 33px 33px;
        margin: 0 16px 0;
        max-height: 40px;
        flex: 1 1 auto;
    }

    &__list {
        padding: 20px 0;

        @media (max-width: 480px) {
            display: flex;
            justify-content: space-between;
            padding: 0 21px;
        }
    }

    &__item {
        padding: 10px 0 10px 4px;

        @media (max-width: 768px) {
            padding: 14px 0 14px 4px;
        }

        @media (max-width: 480px) {
            padding: 5px 0;
        }

        &.active {
            position: relative;

            &::before,
            &::after {
                content: "";
                position: absolute;
                background: #DAEAE8;
                width: 40px;
                height: 40px;
                border-radius: 50%;
                right: 0;
                z-index: 2;

                @media (max-width: 768px) {
                    width: 30px;
                    height: 30px;
                }   
                
                
                @media (max-width: 480px) {
                    display: none;
                }
            }

            &::before {
                bottom: 85%;

                @media (max-width: 768px) {
                    bottom: 79%;
                }
            }

            &::after {
                top: 84%;

                @media (max-width: 768px) {
                    top: 79%;
                }
            }

            .sidebar__link {
                background: #fff;
                color: #64B6AC;
                position: relative;
                z-index: 1;

                @media (max-width: 480px) {
                    background: none;
                }

                &::before {
                    content: "";
                    position: absolute;
                    width: 15px;
                    height: 90px;
                    right: 0;
                    top: 50%;
                    transform: translate(0, -50%);
                    background: #fff;

                    @media (max-width: 768px) {
                        height: 70px;
                    }

                    @media (max-width: 480px) {
                        width: 30px;
                        height: 30px;
                        border-radius: 50%;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        z-index: -1;
                    }
                }

                svg {
                    stroke: #64B6AC;
                }

            }
        }
    }

    &__link {
        display: flex;
        align-items: center;
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 1rem;
        padding: 10px 0 10px 20px;
        border-radius: 50px 0 0 50px;
        transition: all .2s linear;

        &:hover {
            color: #64B6AC;

            svg {
                stroke: #64B6AC;
            }
        }

        @media (max-width: 768px) {
            padding: 6px 0 6px 10px;
        }

        @media (max-width: 480px) {
            padding: 6px 5px 6px 5px;
        }

        svg {
            width: 24px;
            height: 24px;
            stroke: #000;
            transition: all .2s linear;

            @media (max-width: 480px) {
                width: 20px;
                height: 20px;
            }
        }

        span {
            margin-left: 10px;

            @media (max-width: 768px) {
                display: none;
            }
        }
    }
}
