.filter {
    margin-top: 20px;
    display: flex;
    align-items: end;
    justify-content: space-between;
    padding: 14px 26px 18px 14px;
    background: var(--white);
    border-radius: 20px;
    box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
    grid-gap: 20px;

    @media (max-width: 1240px) {
      flex-wrap: wrap;
    }

    @media (max-width: 768px) {
        padding: 27px 26px 27px 20px;
    }

    @media (max-width: 480px) {
        padding: 11px 13px;
    }

    .form__title {
        font-size: 16px;
    }

    .form__btns {
        @media (max-width: 480px) {
            height: unset;
        }
    }

    &__info {
        display: flex;
        align-items: flex-end;
        grid-gap: 40px;
        padding-right: 10px;
        flex: 1 1 auto;

        @media (max-width: 1600px) {
            grid-gap: 10px;
        }

        @media (max-width: 1250px) {
            flex-wrap: wrap;
            grid-gap: 24px;
        }

        @media (max-width: 480px) {
            grid-gap: 15px;
        }
    }

    &__form {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
    }

    &__info-block {
        display: flex;
        flex-direction: column;
        grid-gap: 7px;

        &:first-child {
            flex: 0 1 450px;

            @media (max-width: 1250px) {
                flex: 0 0 100%;
            }
        }

        &:nth-child(2) {

            @media (max-width: 1250px) {
                flex: 0 0 100%;
            }
        }

        &:nth-child(4) {

            @media (max-width: 480px) {
                margin: -40px 0 0 -73px;
                transform: translate(145px, -20px);
            }
        }
    }

    &__select {
        width: 124px;
        height: 44px;
        border: 2px solid var(--light-green);
        border-radius: 40px;
    
        &--catalog {
            width: 100%;
          min-width: 200px;

          &::before {
            position: absolute;
            content: '';
            mask: url("../img/sprite/folder-open.svg");
            mask-position: center;
            mask-repeat: no-repeat;
            background: #BDDEDA;
            width: 24px;
            height: 24px;
            top: 50%;
            left: 14px;
            transform: translateY(-50%);
            z-index: 5;
          }
        }

        &--time {
            width: 132px;
            margin-left: -25px;

            @media (max-width: 1600px) {
                margin-left: 0;
            }
  
            &::before {
              position: absolute;
              content: '';
              mask: url("../img/sprite/clock.svg");
              mask-position: center;
              mask-repeat: no-repeat;
              background: #BDDEDA;
              width: 24px;
              height: 24px;
              top: 50%;
              left: 14px;
              transform: translateY(-50%);
              z-index: 5;
            }
          }
    }

    &__select-header {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        padding: 0 15px 0 45px;
        cursor: pointer;
        font-family: var(--font-family-futura);
    
        svg {
          margin-left: 18px;
          transform: rotate(-90deg);
          fill: var(--green);
        }
    }

    &__select-current {
        min-width: 27px;

        @media (max-width: 768px) {
            font-size: 16px;
        }
    }

    &__select-body {
        display: none;
        flex-direction: column;
        grid-gap: 3px;
        margin-top: 10px;
        width: 100%;
        padding: 10px 0;
        background-color: var(--white);
        border-radius: 12px;
        box-shadow: 0px 4px 15px rgba(212, 212, 212, 0.64);
    }

    &__select-item {
        height: 25px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    
        &:hover {
          background-color: rgba(100, 182, 172, 0.3);
          color: var(--green);
        }
    }

    &__checkbox-label {
        height: 100%;
        position: relative;
        display: flex;
        align-items: center;
        margin-bottom: 12px;

        @media (max-width: 1250px) {
            margin-bottom: 0;
        }
    }

    &__checkbox-input {
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: all 0.3s ease;

        &:checked ~ .filter__checkbox-name::after{
        opacity: 1;
        }

        &:checked ~ .filter__checkbox-name::before{
        background-color: var(--white);
        border: none;
        }
    }

    &__checkbox-name {
        position: relative;
        padding-left: 34px;
        font-size: 18px;
        color: var(--gray);
        transition: all 0.3s ease;
        cursor: pointer;
        white-space: nowrap;

        &::before {
        content: "";
        position: absolute;
        display: block;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        background-color: var(--white);
        width: 24px;
        height: 24px;
        border: 2px solid var(--light-green);
        border-radius: 6px;
        transition: all .3s ease;
        z-index: 1;
        }

        &::after {
        content: "";
        position: absolute;
        display: block;
        background-image: var(--arrow-check);
        background-size: cover;
        background-repeat: no-repeat;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        height: 24px;
        z-index: 5;
        opacity: 0;
        transition: all 0.3s ease;
        }

        &:hover {
            transition: all 0.3s ease;

            &::before {
                background-color: var(--light-green);
            }
        }

        @media (max-width: 768px) {
            font-size: 16px;
        }
    }

    &__input-block {
        display: flex;
        align-items: center;
        column-gap: 14px;
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 18px;
        line-height: 19px;

        @media (max-width: 768px) {
            font-size: 16px;
        }

        @media (max-width: 480px) {
            flex-wrap: wrap;
        }
    }

    &__input-name {
        white-space: nowrap;

        @media (max-width: 480px) {
            flex: 0 0 100%;
        }
    }

    &__input {
        height: 44px;
        width: 60px;
        border: 2px solid var(--light-green);
        border-radius: 40px;
        text-align: center;

        @media (max-width: 768px) {
            font-size: 16px;
        }
    }
}